@charset "UTF-8";//允许中文注释
*{
	margin: 0;
	padding: 0;
}
//轮播图结构 

.banner{
	width: 100%;
	height: 380px;

	.ban_imgs{
		width: 100%;
		height: 380px;
		position: relative;
		margin: 0 auto;
		background-color: #F0F0F0;
			.ban_imgs_img{//图片盒子部分
				width: 100%;
				height: 380px;
				position: absolute;
				background-repeat: no-repeat; 
				background-size: 100% 100%;
				opacity:0;
			}
	};
	
	 .btn_index{//数字按钮
			position: absolute;
			left: 50%;
			top: 90%;
			transform: translate(-50%, -50%);
			.li{
				float: left;
				margin:0 5px;
				width: 20px;
				height: 20px;
				border-radius: 50%;
				text-align: center;
				line-height: 20px;
				font-size: 12px;
				color: #fff;
				list-style: none;
				cursor:pointer;
				background:#666;
			}
			
		}
		
		%act_left_right{
			width: 40px;
			height: 100px;
			top: 130px;
			position: absolute;
			opacity: 0.5;
		}
		.act_left{//左箭头 继承共同样式
			@extend %act_left_right;
			left: 140px;
			background: url(../img/active-left.jpg) no-repeat center center;
		}
		
		.act_right{//右箭头
			@extend %act_left_right;
			right: 140px;
			background: url(../img/active-right.png)  no-repeat center center;
		}
		.act_left:hover{
			opacity: 1;
		}
		.act_right:hover{
			opacity: 1;
		}
	
	
}
//定义一个类加给轮播图
.active_num{
	 background-color: #00bbd7 !important;
}

/* 顶部悬浮 */
.top_of{
	position: fixed;
	z-index: 999;
	background-color: #fff;
	border-bottom: 2px solid rgb(0, 187, 215);
	top: -100px;
	padding: 0;
}

.main .main_content .longimg{
	width: 1200px;
	height: 170px;
	margin-bottom: 30px;
	background-repeat: no-repeat;
	background-position: center center; 
	
	background-size: 100% 100%;
}

//数据图片区域 
.main{
	width: 100%;
	min-height: 2000px;
	background-color: #f3f3f3;
	.main_content{
		width: 1226px;
		margin: 0 auto;
	}
}
.act_louti{
	background-color:rgb(0, 187, 215) !important;
}
.main .left_louti{//楼梯竖直导航部分
		position: fixed;
		top: 30%;
		display: none;
		margin-left: -50px;
		
		a{
			display: block;
			width: 40px;
			height: 40px;
			cursor: pointer;
			background-color: #626262;
			margin-bottom: 2px;
			font-size: 14px;
			color: white;
			line-height: 40px;
			text-align: center;
		}
		.backtop{
			background: url(../img/index_top.jpg) no-repeat;
		}
}

.main .main_content .section{
	width: 1200px;
	height: 426px;
	margin-bottom: 30px;
}
.main_content .section h2{
	font-size: 24px;
	font-weight: 600;
	display: block;
	overflow: hidden;
	position: relative;
}
.main_content .section h3{
	font-size: 24px;
	font-weight: 600;
	height: 40px;
}
.main_content .section .nav_p{
	overflow: hidden;
	background: url(https://www.sundan.com/themes/shundian/images/index_bottom_l.png) repeat-x scroll bottom left;
		p{
			background: url(https://www.sundan.com/themes/shundian/images/index_bottom_d.png)repeat-x scroll bottom left;
			height: 25px;
			width: 100px;
			float: left;
			font-size: 14px;
			color: #919191;
		}
}

.main_content .section .nav_p a{
	float: right;
	color: #919191;
	margin-left: 40px;
	font-size: 14px;
}


.section_img ul{
	display: flex;
	justify-content: space-between;
	align-items: center;
	li{
		width: 290px;
		height: 350px;
		background-color: #fff;
		margin-top: 10px;
		a{
			display: block;
			width: 200px;
			margin: 30px 0 0 45px;
			img{
				width: 200px;
				height: 200px;
			}
			p{
				padding: 0 2px;
				color: #444444;
				font-size: 14px;
				text-overflow: ellipsis;
				overflow: hidden;
				white-space: nowrap;
				line-height: 40px;
			}
			span{
				color:#fc3538;
				font-weight: 600;
				font-size: 14px;
			}
			
		};
	}
}
.section_img ul li:hover{
	 margin-top:-3px;
	 box-shadow: 0 0 5px #999999;
}

